"use client"

import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Plus, Edit2, Trash2, Users } from "lucide-react"
import Link from "next/link"
import { useState } from "react"

interface User {
  id: string
  name: string
  email: string
  role: "admin" | "user"
  status: "active" | "inactive"
  createdAt: string
}

export default function AdminUsersPage() {
  const [users, setUsers] = useState<User[]>([])

  const handleDelete = (id: string) => {
    setUsers(users.filter((u) => u.id !== id))
  }

  return (
    <div className="p-8 bg-gray-50 min-h-screen">
      <div className="max-w-6xl mx-auto space-y-8">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Users</h1>
            <p className="text-gray-600 mt-2">Manage all users and permissions</p>
          </div>
          <Button asChild>
            <Link href="/app/admin/users/new" className="gap-2">
              <Plus size={18} />
              Add User
            </Link>
          </Button>
        </div>

        {/* Stats */}
        <div className="grid md:grid-cols-3 gap-6">
          <Card className="border-gray-200 p-6 bg-white">
            <p className="text-sm font-medium text-gray-600 mb-2">Total Users</p>
            <p className="text-3xl font-bold text-gray-900">{users.length}</p>
          </Card>
          <Card className="border-gray-200 p-6 bg-white">
            <p className="text-sm font-medium text-gray-600 mb-2">Active</p>
            <p className="text-3xl font-bold text-gray-900">{users.filter((u) => u.status === "active").length}</p>
          </Card>
          <Card className="border-gray-200 p-6 bg-white">
            <p className="text-sm font-medium text-gray-600 mb-2">Admins</p>
            <p className="text-3xl font-bold text-gray-900">{users.filter((u) => u.role === "admin").length}</p>
          </Card>
        </div>

        {/* Users Table */}
        {users.length === 0 ? (
          <Card className="border-gray-200 p-12 text-center bg-white">
            <Users className="w-12 h-12 text-gray-400 mx-auto mb-4" />
            <h2 className="text-2xl font-bold text-gray-900 mb-3">No users yet</h2>
            <p className="text-gray-600 mb-6">Add your first user to get started</p>
            <Button asChild>
              <Link href="/app/admin/users/new" className="gap-2">
                <Plus size={18} />
                Add User
              </Link>
            </Button>
          </Card>
        ) : (
          <Card className="border-gray-200 bg-white overflow-hidden">
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead className="border-b border-gray-200 bg-gray-50">
                  <tr>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-gray-900">Name</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-gray-900">Email</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-gray-900">Role</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-gray-900">Status</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-gray-900">Created</th>
                    <th className="text-right px-6 py-4 text-sm font-semibold text-gray-900">Actions</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {users.map((user) => (
                    <tr key={user.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4 text-sm font-medium text-gray-900">{user.name}</td>
                      <td className="px-6 py-4 text-sm text-gray-600">{user.email}</td>
                      <td className="px-6 py-4 text-sm capitalize">
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                          {user.role}
                        </span>
                      </td>
                      <td className="px-6 py-4 text-sm">
                        <span
                          className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                            user.status === "active" ? "bg-green-100 text-green-800" : "bg-gray-100 text-gray-800"
                          }`}
                        >
                          {user.status === "active" ? "Active" : "Inactive"}
                        </span>
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">{user.createdAt}</td>
                      <td className="px-6 py-4 text-right">
                        <div className="flex items-center justify-end gap-2">
                          <Link
                            href={`/app/admin/users/${user.id}/edit`}
                            className="p-1.5 text-gray-600 hover:bg-gray-100 rounded-lg transition"
                          >
                            <Edit2 size={18} />
                          </Link>
                          <button
                            onClick={() => handleDelete(user.id)}
                            className="p-1.5 text-gray-600 hover:bg-red-100 hover:text-red-600 rounded-lg transition"
                          >
                            <Trash2 size={18} />
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </Card>
        )}
      </div>
    </div>
  )
}
